.about {
  padding: 40px;
  border-radius: 10px;
  position: absolute;
  z-index: 2;
  background-color: white;
  width: 50vw;
  min-height: 30vw;
  left: 50vw;
  margin-left: -25vw;
  top: 60px;
  animation-name: move;
  animation-duration: 0.5s;
}

.x {
  font-size: 1.2em;
  border-radius: 50%;
  position: absolute;
  top: 2px;
  right: 5px;
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: bold;
}

.x:hover {
    color: rgb(106, 6, 6);
    cursor: pointer;
  }

@keyframes move {
  from {
    opacity: 0;
    top: -500px;
  }
  to {
    opacity: 1;
    top: 60px;
  }
}

@media(max-width: 800px){
    .about{
        width: 70%;
        margin-left: -35%;
        padding: 10px;
    }
}
